@import "../../styles/colors";

.list-group {
  list-style: none;
  margin: 0;
  padding: 0;

  width: 100%;
  .not-found {
    padding: 0.5rem;
  }
  li {
    border: 1px solid $gray-100;
    display: flex;
    justify-content: space-between;
    align-items: center;
    cursor: pointer;
    &:hover {
      background-color: $yellow;
    }
    
    a {
      width: 100%;
      height: 100%;
      text-decoration: inherit;
      color: inherit;
      display: flex;
      & > * {
        padding: 0.5em;
      }
      .id {
        border-right: 1px solid $gray-100;
        min-width: 3rem;
        display: flex;
        justify-content: center;
      }
      .name {
        flex: 1;
        text-transform: capitalize;
      }
    }
    &:not(:first-child) {
      border-top: none;
    }
    &:first-child,
    &:first-child:hover {
      border-radius: 0.5rem 0.5rem 0 0;
    }
    &:last-child,
    &:last-child:hover {
      border-radius: 0 0 0.5rem 0.5rem;
    }

    &:only-child,
    &:only-child:hover {
      border-radius: 0.5rem;
    }
  }
}
